import { Select } from './select/select'
import './select/style.scss'

export const selectedValue = {}
export const selectedSize = {}
export const selectedSous = {}

const price = 10;
let finishPrice = 0;

const select = new Select('#select', {
  placeholder: '–í—ã–±–µ—Ä–∏ –ø–æ–∂–∞–ª—É–π—Å—Ç–∞ –≤–∏–¥ –ø–∏—Ü—Ü—ã',
  data: [
    { id: '1', value: '–ü–µ–ø–ø–µ—Ä–æ–Ω–∏', price: price },
    { id: '2', value: '–ü—Ä–æ—à—É—Ç–æ', price: price },
    { id: '3', value: '–ö—É—Ä–∏–Ω–∞—è', price: price },
    { id: '4', value: '–î–µ—Ä–µ–≤–µ–Ω—Å–∫–∞—è', price: price }
  ],
  onSelect(item) {
    selectedValue.pizza = {
      value: item.value,
      price: item.price
    }
    disabledSize.removeAttribute("disabled", "disabled")
    finishPrice += item.price
  }
})

const selectSize = new Select('#selectSize', {
  placeholder: '–í—ã–±–µ—Ä–∏ –ø–æ–∂–∞–ª—É–π—Å—Ç–∞ —Ä–∞–∑–º–µ—Ä –ø–∏—Ü—Ü—ã',
  data: [
    { id: '1', value: '16 cm ', price: 0 },
    { id: '2', value: '24 cm ', price: 3 },
    { id: '3', value: '30 cm ', price: 6 },
    { id: '4', value: '35 cm ', price: 10 }
  ],
  onSelect(item) {
    selectedSize.size = {
      value: item.value,
      price: item.price,
    }
    disabledSous.removeAttribute("disabled", "disabled")
    finishPrice += item.price
  }
})

const selectSous = new Select('#selectSous', {
  placeholder: '–í—ã–±–µ—Ä–∏ –ø–æ–∂–∞–ª—É–π—Å—Ç–∞ –≤–∏–¥ —Å–æ—É—Å–∞',
  data: [
    { id: '1', value: '–°—ã—Ä–Ω—ã–π', price: 1 },
    { id: '2', value: '–ö–∏—Å–ª–æ-—Å–ª–∞–¥–∫–∏–π', price: 1 },
    { id: '3', value: '–ë–∞—Ä–±–µ–∫—é', price: 1 },
    { id: '4', value: '–ß–µ—Å–Ω–æ—á–Ω—ã–π', price: 1 }
  ],
  onSelect(item) {
    selectedSous.sous = {
      value: item.value,
      price: item.price
    }
    finishPrice += item.price
  }
})

const btn = document.getElementById('btn')

btn.onclick = () => {
  const pizza = document.getElementById('pizza')
  pizza.innerHTML = `–í–∞—à–∞ –ø–∏—Ü—Ü–∞ - " ${selectedValue.pizza.value} " (—Å—Ç–æ–∏–º–æ—Å—Ç—å –ø–∏—Ü—Ü—ã - ${selectedValue.pizza.price} BYN)`

  const size = document.getElementById('size')
  size.innerHTML = `–í–∞—à —Ä–∞–∑–º–µ—Ä - " ${selectedSize.size.value} " (–¥–æ–ø–ª–∞—Ç–∞ –∑–∞ —Ä–∞–∑–º–µ—Ä + ${selectedSize.size.price} BYN)`

  const sous = document.getElementById('sous')
  sous.innerHTML = `–í–∞—à —Å–æ—É—Å - " ${selectedSous.sous.value} " (–¥–æ–ø–ª–∞—Ç–∞ –∑–∞ —Å–æ—É—Å + ${selectedSous.sous.price} BYN)`

  const sum = document.getElementById('sum')
  sum.innerHTML = `–°—É–º–º–∞ –í–∞—à–µ–≥–æ –∑–∞–∫–∞–∑–∞ - " ${finishPrice} BYN "`
  finishPrice = 0
}

const disabledSous = document.getElementById('selectSous')
const disabledSize = document.getElementById('selectSize')

const selectedPlaceholder = document.getElementById('placeholder')
if (selectedPlaceholder.innerHTML === '–í—ã–±–µ—Ä–∏ –ø–æ–∂–∞–ª—É–π—Å—Ç–∞ –≤–∏–¥ –ø–∏—Ü—Ü—ã') {
  disabledSize.setAttribute("disabled", "disabled")
  disabledSous.setAttribute("disabled", "disabled")
}